@media screen and (max-width: 600px) {
    .navbar Link:not(:first-child) {display: none;}
    .navbar Link.navbar-brand {
      float: right;
      display: block;
    }
  }

  @media screen and (max-width: 600px) {
    .navbar.responsive {position: relative;}
    .navbar.responsive Link.navbar-brand {
      position: absolute;
      right: 0;
      top: 0;
    }
    .navbar .navbar-brand{
      position: relative;
      top: 0;
      right: 0;
      display: block;
      text-align: left;
    }

    .navbar .navbar-brand .logo-img{
      position: absolute;
      top: 0;
      right: 0;
    }

    .navbar.responsive :link {
      float: none;
      display: block;
      text-align: left;
    }
  }

